.container{
  display: flex;
  padding: 0px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Rubik', sans-serif;
  background: radial-gradient(circle at top, #9da7cd , #060a1e);
  height: 100vh;
  overflow-y: hidden;

}
.bienvenida{
  font-size: 30px;
  font-weight: 200;
  color: rgba(195, 227, 240, 0.938);
  text-align: center;
  margin-top: 40%;
}
.input{
  padding:10px;
  width: 70%;
  border: 1px solid skyblue;
  border-radius: 10px;
  font-size: 16px;
  box-shadow: 5px 6px 35px -3px rgba(0,0,0,0.87);
-webkit-box-shadow: 5px 6px 35px -3px rgba(0,0,0,0.87);
-moz-box-shadow: 5px 6px 35px -3px rgba(0,0,0,0.87);
}
input:focus{
  outline: none;
}
.weather-data{
  display: flex;
  width: 50%;
  flex-direction: column;
  margin: auto;
  align-items: center;
  background-color: rgba(120, 208, 243, 0.651);
  border-radius: 10px;
  padding: 20px;
  box-shadow: 5px 6px 35px -3px rgba(0,0,0,0.87);
-webkit-box-shadow: 5px 6px 35px -3px rgba(0,0,0,0.87);
-moz-box-shadow: 5px 6px 35px -3px rgba(0,0,0,0.87);
}
.city{
  font-size: 50px;
  font-weight: bold;
}
.temp{
  font-size: 90px;
  padding: 5px;
  border: 3px solid rgb(2, 2, 2);
  border-radius: 16px;
  margin: auto;
  font-weight: bold;
}
.weather{
  font-size: 26px;
  font-weight: bold;
}
.viento{
  font-size: 30px;
  margin: auto;
}
.maxima{
  font-size: 20px;
  margin: 40px
}
.minima{
  font-size: 20px;
  margin: 40px
}